.App {
  display: flex;
  width: 1200px;
  height: 100%;
  align-items: left;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
  margin: 0 auto;
 background-color: rgb(242, 248, 248);
 font-family: Arial, Helvetica, sans-serif;
}

nav{
 padding-top: 50px;
  width: 100%;
  height: 180px;
 border-bottom: solid rgb(214, 204, 204) 1px;
  padding-right: 10px;
  margin-bottom: 80px;
 
}

.search{
 background-color: rgb(242, 248, 248);
  width: 350px;
  border:solid rgb(214, 204, 204) 1px;
  display: flex;
  flex-direction: row;
  margin:0 auto ;
  float: right;
  border-radius: 25px;
 
}

i{
 color: rgb(47, 13, 238);
 margin-left: -40px;
 font-size: 20px;
 justify-content: center;
 position:absolute;
 margin-top: 8px;
 
}

input:hover{
  font-size: large;
}

input{
  box-shadow: 0 0 0 0;
  outline: 0;
  width:90%;
  height: 35px;
 align-items: center;
 justify-items: center;
  border: none;
 color: black;
 font-size: medium;
 background-color: rgb(242, 248, 248);
 text-transform: capitalize;
}
input::placeholder {
  color: rgb(173, 171, 171);
}

label{
  
  width: 350px;
  text-align: center;
  border-radius: 20px;
}

.Alimento{
  border-radius: 20px;
 color: rgba(8, 8, 8, 0.753);
 width: 300px;
 height: 300px;
 margin: 0;
 display: flex;
 padding-left: 10px;
 flex-direction: column;
 background-color: #e0dede;
 justify-content: center;
 
}
.Alimento:hover{
  background-color: #b9e2f0;
  font-size: larger;
  transform: scale(1.0);
  cursor: pointer;
  
}

h5, h4{
  margin: 0;
}

span{
  color: rgb(0, 0, 0);
}

.logo{
  color: rgb(255, 0, 0);
  width: 250px;
  margin-left: 100px;

}

p{
  width: 250px;
  height: 85px;
  text-align: left;
  border:solid rgb(214, 204, 204) 1px;
  margin-left: 100px;
  font-size: 15px;
  padding: 5px;
  border-radius: 10px;
  padding-top: 15px;
  color: rgb(2, 2, 2);
}



@media only screen and (max-width:720px){
  .App{
    width: 100%;
     display: flex;
     flex-wrap: wrap;
    height: auto;
   
    
  }

nav{
  padding-top: 0px;
  width: auto;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 2px;
  padding-bottom: 5px;
  
}

inpu{
  text-transform: capitalize;
}

.logo, p{

 padding-top: 10px;
 margin: 0 auto;
 padding-bottom: 10px;
}


.search{
  margin: 0 auto;
  margin-top: 30px;
  margin-bottom: 20px;
 padding: 3px;
}
i{

margin-top: 12px;

}

}